* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative; }

nav {
  height: 50px;
  background-color: #212121; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    font-size: 15px;
    font-weight: 300;
    color: #ffffff;
    padding-right: 28px; }

  .menu_item {
    border-right: 2px dotted black; } }

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_official {
    font-size: 13px;
    font-weight: 100;
    color: rgba(255, 255, 255, 0.5); }

  .subheader_call {
    margin-top: 19px;
    font-size: 12px;
    font-weight: 300;
    color: #ffffff;
    text-align: right; }

  .subheader_phone {
    display: block;
    text-align: right;
    margin-top: 8px;
    font-size: 22px;
    color: #ffffff;
    line-height: 12px; }

  .subheader_btn {
    width: 172px;
    height: 46px;
    box-shadow: inset 0px -3px 0 0 rgba(0, 0, 0, 0.12);
    border: none;
    background-image: linear-gradient(to top, #1eacc7, #4ce2ff);
    margin-top: 19px;
    text-transform: uppercase;
    border-radius: 4px;
    text-shadow: 0.7px 0.7px 0 rgba(0, 0, 0, 0.21);
    font-size: 14px;
    color: #ffffff; } }

.promo {
  min-height: 800px;
  background: url(../img/bg.jpg);
  padding: 151px 0 0 0; }

.promo_subheader {
  font-size: 36px;
  color: #ffffff;
  text-align: center;
  font-weight: bold;
  line-height: 36px;
  font-weight: 700;
  margin: 0;
  margin-top: 22px; }

.promo_header {
  text-align: center;
  font-size: 60px;
  font-weight: 700;
  color: #ffffff;
  line-height: 60px;
  margin: 0; }

.promo_descr {
  text-align: center;
  font-size: 18px;
  text-align: center;
  color: #ffffff;
  margin: 0;
  margin-top: 23px; }

.promo_btn {
  display: block;
  margin: 118px auto 0 auto;
  width: 259px;
  height: 63px;
  box-shadow: inset 0px -4px 0 0 rgba(0, 0, 0, 0.12);
  border: none;
  border-radius: 4px;
  background-image: linear-gradient(to top, #1eacc7, #4ce2ff);
  font-size: 18px;
  color: #ffffff;
  font-weight: 400; }

.reasons {
  padding: 71px 0 65px 0; }

.label {
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center; }

.title {
  margin-bottom: 0;
  margin-top: 38px;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center; }

.reasons .reasons_block {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;

  .reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%; }

  .reasons_descr {
    width: 343px;

    .reasons_subtitle {
      line-height: 21px;
      color: #1eacc7;
      font-size: 17px;
      font-weight: 400; }

    .reasons_text {
      color: #202020;
      font-size: 15px;
      font-weight: 300;
      line-height: 21px; } } }
